* {
	box-sizing: border-box; }
.container {
	background: #f8f8f8;
	width: 700px;
	position: absolute;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 50px 60px 50px; }

$select-color: #a7a7a8;
$select-bg-not: #fff;
$select-border-color: #e2e2e2;
$select-font: 400 15px/1em Arial, sans-serif;
$select-active-li: #eaeaea;
$select-hover-li: #f9f9f9;
$select_wrapper-width: 200px;
.select-view {
	background: $select-bg-not;
	border: 1px solid $select-border-color;
	color: $select-color;
	cursor: pointer;
	font: $select-font;
	height: 50px;
	padding: 1em 20px;
	position: relative;
	margin-bottom: 5px;
	&:after {
		transition: all .3s;
		content: '';
		position: absolute;
		right: 20px;
		top: 50%;
		transform: translateY(-50%);
		@include arr(8, 7, $select-color, b); } }
.select_wrapper {
	width: $select_wrapper-width;
	position: relative;
	.active {
		&:after {
			transform: rotateX(180deg); } } }
ul.select-options {
	background: $select-bg-not;
	position: absolute;
	display: none;
	cursor: pointer;
	border: 1px solid #e2e2e2;
	z-index: 1;
	width: 100%;
	// height: 200px
	// overflow-y: scroll
	li {
		color: $select-color;
		font: $select-font;
		padding: 10px 20px;
		transition: background .4s;
		&[rel="hide"] {
			display: none; }
		&.active {
			background: $select-active-li;
			&:hover {
				background: $select-active-li; } }
		&:hover {
			background: $select-hover-li; } } }
.select-hidden {
	display: none; }

